/* stylelint-disable */
/* stylelint-enable */
*,
*::before,
*::after {
  box-sizing: border-box;
}

body,
h1,
.h1,
h2,
.h2,
h3,
.h3,
h4,
.h4,
p,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

ul[role=list],
ol[role=list] {
  list-style: none;
}

html:focus-within {
  scroll-behavior: smooth;
}

body {
  min-height: 100vh;
  background-color: var(--ui-02, #eeeef1);
  font-family: "Montserrat", sans-serif;
  font-size: 0.875rem;
  font-weight: 500;
  line-height: 1.714;
  color: var(--text-default, #161616);
  text-align: left;
  text-rendering: optimizeSpeed;
}

hr {
  height: 0.0625rem;
  margin: 1rem 0;
  color: var(--ui-04, #d5d6e0);
  background-color: currentColor;
  border: 0;
}

h6, .h6, h5, .h5, h4, .h4, h3, .h3, h2, .h2, h1, .h1 {
  margin-top: 0;
  margin-bottom: 0.5rem;
}

h1, .h1 {
  font-size: 1.75rem;
  line-height: 1.357em;
  font-weight: 700;
  letter-spacing: 0.009em;
}
@media (max-width: 1280px) {
  h1, .h1 {
    font-size: calc(1.075rem + 0.84375vw);
  }
}

h2, .h2 {
  font-size: 1.5rem;
  line-height: 1.417em;
  font-weight: 600;
  letter-spacing: 0.008em;
}
@media (max-width: 1280px) {
  h2, .h2 {
    font-size: calc(1.05rem + 0.5625vw);
  }
}

h3, .h3 {
  font-size: 1.25rem;
  line-height: 1.5em;
  font-weight: 600;
  letter-spacing: 0.007em;
}
@media (max-width: 1280px) {
  h3, .h3 {
    font-size: calc(1.025rem + 0.28125vw);
  }
}

h4, .h4 {
  font-size: 1.125rem;
  line-height: 1.556em;
  font-weight: 600;
  letter-spacing: 0.008em;
}
@media (max-width: 1280px) {
  h4, .h4 {
    font-size: calc(1.0125rem + 0.140625vw);
  }
}

h5, .h5 {
  font-size: 1rem;
  line-height: 1.625em;
  font-weight: 600;
  letter-spacing: 0.009em;
}

h6, .h6 {
  font-size: 0.875rem;
  line-height: 1.714em;
  font-weight: 600;
  letter-spacing: 0.011em;
}

p {
  margin-top: 0;
  margin-bottom: 1.5rem;
}

abbr[title],
abbr[data-original-title] {
  text-decoration: underline;
  text-decoration: underline dotted;
  cursor: help;
  border-bottom: 0;
  text-decoration-skip-ink: none;
}

address {
  margin-bottom: 1rem;
  font-style: normal;
  line-height: inherit;
}

ol,
ul,
dl {
  margin-top: 0;
  margin-bottom: 1rem;
}

ol ol,
ul ul,
ol ul,
ul ol {
  margin-bottom: 0;
}

dt {
  font-weight: 700;
}

dd {
  margin-bottom: 0.5rem;
  margin-left: 0;
}

blockquote {
  margin: 0 0 1rem;
}

b,
strong {
  font-weight: bolder;
}

small, .small {
  font-size: 80%;
}

mark, .mark {
  padding: 0.2em;
  background-color: var(--text-muted, #666679);
}

sub,
sup {
  position: relative;
  font-size: 75%;
  line-height: 0;
  vertical-align: baseline;
}

sub {
  bottom: -0.25em;
}

sup {
  top: -0.5em;
}

a:not([class]) {
  color: var(--text-primary, #1078cf);
  text-decoration: underline;
  text-decoration-skip-ink: auto;
}
a:not([class]):hover, a:not([class]):focus {
  color: var(--primary-hover, #1260a1);
  text-decoration: underline;
}

a[class] {
  text-decoration: none;
}
a[class]:hover, a[class]:focus {
  text-decoration: none;
}

pre,
code,
kbd,
samp {
  font-family: SFMono-Regular, Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace;
  font-size: 1em;
}

pre {
  margin-top: 0;
  margin-bottom: 1rem;
  overflow: auto;
}

figure {
  margin: 0 0 1rem;
}

img,
picture {
  max-width: 100%;
  display: block;
}

input,
button,
textarea,
select {
  font: inherit;
}

fieldset {
  min-width: 0;
  padding: 0;
  margin: 0;
  border: 0;
}

legend {
  display: block;
  width: 100%;
  max-width: 100%;
  padding: 0;
  margin-bottom: 0.5rem;
  font-size: 1.5rem;
  line-height: inherit;
  color: inherit;
  white-space: normal;
}
@media (max-width: 1280px) {
  legend {
    font-size: calc(1.05rem + 0.5625vw);
  }
}

progress {
  vertical-align: baseline;
}

table {
  border-collapse: collapse;
}

@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }

  *,
*::before,
*::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
output {
  display: inline-block;
}

summary {
  display: list-item;
  cursor: pointer;
}

template {
  display: none;
}

[hidden] {
  display: none !important;
}

[disabled] {
  pointer-events: auto !important;
  cursor: not-allowed !important;
}

.lead {
  font-size: 1.09375rem;
  font-weight: 300;
}
@media (max-width: 1280px) {
  .lead {
    font-size: calc(1.009375rem + 0.10546875vw);
  }
}

